<template>
    <div class="l_palyStepBox">
        <Theader title="玩法指引"></Theader>
        <div class="block">
            <el-carousel trigger="click" height="150px">
                <el-carousel-item v-for="index in 13" :key="index">
                    <img src="" v-lazy="junImgUrl+'l_imgs/l_step/'+index+'.jpg'" alt="" />
                </el-carousel-item>
            </el-carousel>
        </div>
    </div>
</template>
<script>
    import './palyStep.scss'
    import Theader from '../header/header.vue'
    export default{
        data :() =>({
        }),
        components: {
            // loading
            Theader
        },
        mounted: function() {
            //设置标题
            this.$store.state.headline = "玩法指引";
        },
    }
</script>


